@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .coaches__item-cover--four, .coaches__item-cover--eight {
    background-image: url("../img/coaches/coache-ketova@2x.jpg");
  }
}

.coaches__button-cover {
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  border: none;
  background-color: rgba(255, 255, 255, 0);
  font-size: 18px;
  font-weight: 700;
  line-height: 150%;
  width: 100%;
  height: 100%;
  color: #ffffff;
  padding-bottom: 23px;
}
.coaches__button-cover.focus-visible:focus {
  outline: none;
  border: 1px solid #102654;
}

.coaches__title-item {
  text-align: center;
}
.coaches__title-item--content {
  margin: 0;
  margin-top: 46px;
  color: #ffffff;
  font-size: 18px;
  line-height: 105%;
  font-weight: 700;
}

.coaches__direction {
  text-align: center;
}

@media (min-width: 1366px) and (max-width: 1366px) {
  .coaches__item-cover--first, .coaches__item-cover--five {
    background-size: 260px 390px;
    background-position: 0 -38px;
  }
  .coaches__item-cover--two, .coaches__item-cover--six {
    background-size: 312px 468px;
    background-position: -49px -34px;
  }
  .coaches__item-cover--three, .coaches__item-cover--seven {
    background-size: 260px 462px;
    background-position: 0 -24px;
  }
  .coaches__item-cover--four, .coaches__item-cover--eight {
    background-size: 354px 530px;
    background-position: -47px -55px;
  }
}
@media (max-width: 1199px) {
  .coaches {
    padding-top: 49px;
  }
  .coaches__title {
    font-size: 42px;
    text-align: center;
    letter-spacing: 1px;
    padding-right: 6px;
  }
  .coaches__swiper {
    margin-top: 40px;
  }
  .coaches__list {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin-left: 55px;
    margin-right: 55px;
  }
  .coaches__item::before {
    padding-top: 109%;
  }
}
